.Grid {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.Grid text {
  cursor: default;
}

.Grid .hinted {
  stroke: orange;
  stroke-width: 2;
}

.Grid .solved {
  stroke: black;
  stroke-width: 2;
}

.Grid .hover {
  fill: lightgray;
}



